.navbar .correct {
    -webkit-animation: 1s correct;
            animation: 1s correct;
}
  
.navbar .incorrect {
    -webkit-animation: 1s incorrect;
            animation: 1s incorrect;
}
  
@-webkit-keyframes correct {
    0% {
      color: white;
    }
    20% {
      color: #2e7d32;
    }
    40% {
      color: white;
    }
    60% {
      font-weight: #2e7d32;
    }
    80% {
      font-weight: white;
    }
    100% {
      font-weight: #2e7d32;
    }
}
  
@keyframes correct {
    0% {
      color: white;
    }
    20% {
      color: #2e7d32;
    }
    40% {
      color: white;
    }
    60% {
      font-weight: #2e7d32;
    }
    80% {
      font-weight: white;
    }
    100% {
      font-weight: #2e7d32;
    }
}
  
@-webkit-keyframes incorrect {
    0% {
      color: white;
    }
    20% {
      color: #c62828;
    }
    40% {
      color: white;
    }
    60% {
      font-weight: #c62828;
    }
    80% {
      font-weight: white;
    }
    100% {
      font-weight: #c62828;
    }
}
  
@keyframes incorrect {
    0% {
      color: white;
    }
    20% {
      color: #c62828;
    }
    40% {
      color: white;
    }
    60% {
      font-weight: #c62828;
    }
    80% {
      font-weight: white;
    }
    100% {
      font-weight: #c62828;
    }
}



